import React, { useState } from 'react'
import './App.scss'
import ExperimentStep from './ExperimentStep'
import ImagesFileNames from './ImagesFileNames'
import shuffle from 'shuffle-x'

function App () {
  const fileNames = shuffle(ImagesFileNames)
  const [experimentStepData, setExperimentStepData] = useState({
    currentPhotoIndex: 1,
    remainingPhotos: fileNames.length - 1,
    imageSource: './images/' + fileNames[0]
  })

  const saveData = (imageRating, imageSource, timeStart) => {
    console.log(imageSource, imageRating, timeStart)
    const currentPhotoIndex = experimentStepData.currentPhotoIndex + 1
    setExperimentStepData({
      currentPhotoIndex: currentPhotoIndex,
      remainingPhotos: experimentStepData.remainingPhotos - 1,
      imageSource: './images/' + fileNames[currentPhotoIndex - 1]
    })
  }

  return (
    <ExperimentStep
      imageSource={experimentStepData.imageSource}
      currentPhotoIndex={experimentStepData.currentPhotoIndex}
      remainingPhotos={experimentStepData.remainingPhotos}
      saveData={saveData}
    />
  )
}

export default App
